// General Wrapper for the Header
.top-bar {

}

// Mobile Menu-Bar
.title-bar {
  .title-bar-inner {
	@apply flex flex-row;
	@apply justify-center content-center;
	@apply px-2 py-2 mx-auto;
  }

  .logo-wrap {
	@apply flex-1 text-center;
  }

  .menu-icon {
	@apply cursor-pointer;

	&:before {
	  @extend .trevor-ti-bars-solid:before;
	}
  }

  @screen md {
	@apply hidden;
  }
}

// Top Bar
.top-bar-inner {
  @apply hidden;
  @apply w-full mx-auto;

  @screen md {
	@apply block;
  }
}

@media screen and (max-width: theme('screens.md')) {
  #top-bar-open:checked ~ .top-bar-inner {
	@apply block;
	@apply h-full;

	background-color: #0A246A;

	.main-menu {
	  @apply grid-flow-row;
	}

	.top-bar-ceil,
	.top-bar-floor,
	.logo-wrap {
	  @apply hidden;
	}

	.main-menu-container {
	  @apply w-full;
	  background: red;
	}
  }
}

// Ceil Part
// ----------------------------
.top-bar-ceil {
  @apply py-6;
  @apply flex flex-row align-baseline;

  // Switcher
  .switcher {
	@apply list-none;
	@apply flex flex-row flex-1;

	> li {
	  @apply px-2;

	  &:first-child {
		@apply pl-0;
	  }

	  > a {
		@apply pb-2;
		@apply border-b-4 border-black;

		&.active {
		  @apply text-blue-dark;
		  @apply border-blue-dark;
		}
	  }
	}
  }

  // CTAs
  .cta-wrap {

  }
}


// Center Part
// ----------------------------
.top-bar-center {
  @apply flex flex-row;

  .logo-wrap {
	@apply flex-1;

	> .logo {
	  @apply text-blue-dark text-3xl font-bold;
	}
  }
}

// Main Menu
/* https://moderncss.dev/css-only-accessible-dropdown-navigation-menu/ */
.main-menu-container {
  @apply self-end;
  @apply pb-4;
  $general-transition: 280ms all 120ms ease-out;

  // Convert lists to grid
  ul {
	@apply grid;
	@apply list-none m-0 p-0;

	li {
	  @apply p-0;
	}
  }

  li {
	@apply block;
	@apply cursor-pointer;
  }

  .main-menu {
	@apply grid-flow-col;
	@apply gap-x-6;

	// First tier
	> li {
	  > a {
		@apply uppercase;
	  }
	}
  }

  // Dropdowns
  @screen md {
	.menu-item-has-children {
	  @apply relative;

	  // Submenu icon
	  &:after {
		@extend .trevor-ti-angle-down-solid:before;
		@apply ml-1;
		transition: $general-transition;
	  }

	  .sub-menu {
		position: absolute;
		min-width: 230px;
		left: 50%;
		top: calc(100% - 0.25rem);
		transition: $general-transition;
		transform: rotateX(-90deg) translateX(-50%);
		transform-origin: top center;
		visibility: hidden;
		opacity: 0.3;
		padding: 0.5em 0;
		background-color: #fff;
		@apply shadow;

		//
		@apply mt-2;
		@apply border-t-4 border-blue-dark;

		li {
		  > a {
			@apply py-4 px-4 w-full;
			@apply block;

			.title-wrap {
			  @apply text-blue-dark underline;
			}

			&:hover {
			  @apply bg-white;
			}
		  }
		}

		.subtitle {
		  @apply mt-2;
		  @apply text-black text-sm;
		}
	  }

	  &:hover,
	  &:focus-within {
		.sub-menu {
		  opacity: 1;
		  transform: rotateX(0) translateX(-50%);
		  visibility: visible;

		  a {
			opacity: 1;
		  }
		}

		&:after {
		  opacity: 1;
		}
	  }

	}


  }
}


// Bottom Part
// ----------------------------
.top-bar-bottom {

